.sidebar{
  
    height: 100vh;
    flex: 0.2;
    color: white;
    min-width: 230px;
    background-color: #040404;
    padding-left: 10px;
    padding-right: 10px;
}

.sidebar > hr {
    border: 1px solid #282828;
    width: 90%;
    margin: 10px auto;
}

.sidebar__logo{
    height: 70px;
   padding: 10px;
   margin-right: auto;
}
.sidebar__title{
    margin-left: 10px;
    padding: 5px;
    font-size: 12px;
}